import axios from 'axios'

export default new class {
    async getCurrencyCollection() {
        const path = '/currency-collection/data/currencyCollection.json'
        
        console.log('local-data-api ➤➤➤ ', path)
               
        return axios.get(path, { // return axios.get('/mock/xpackages.json')
            headers: {
            'Content-Type': 'application/json'
            }
        })
    }

    async getCurrencyAssets(collection: any) {
        const path = 'https://gdroid.gleeze.com/api/External/Forex/CalculateAssets/aud?variationPercent=0.12'
        
        console.log('gdroid-data-api ➤➤➤ ', path)
               
        return axios.post(path, collection, { // return axios.get('/mock/xpackages.json')
            headers: {
            'Content-Type': 'application/json',
            'X-Access-Token': 'aa2a368c03044b15a228dfc6bc639efe'
            },
        })
    }
}