<script lang="ts">
import { Options, Vue } from 'vue-class-component';
import XPackages from '@/components/XPackages.vue';
import XPackage from '@/components/XPackage.vue';
import XSummary from '@/components/XSummary.vue';
import dataService from '@/service/data.service';

@Options({
  components: {
    XPackages,
    XPackage,
    XSummary
  },
  data () {
    return {
      xpkgs: null,
      xpkg: null
    }
  },
  created () {
      this.fetchEventsList();
      this.timer = setInterval(this.fetchEventsList, 10000);
  },
  methods: {
      fetchEventsList () {
          dataService.getCurrencyCollection().then(response => {
            dataService.getCurrencyAssets(response.data).then(finalResponse => {    
              this.xpkgs = finalResponse.data
              console.log('api-call > xpkgs', this.xpkgs)
            }).catch( (err) => {
              console.error('error > getCurrencyAssets', err)
            });
          })
          .catch( (err) => {
            console.error('error > getCurrencyCollection', err)
          });
      },
      cancelAutoUpdate () {
          clearInterval(this.timer)
      }
  },
  beforeUnmount () {
    this.cancelAutoUpdate();
  },
})
export default class HomeView extends Vue {}
</script>

<template>
  <div class="root">
    <x-summary :xpkgs=xpkgs v-if="xpkgs"  />
    <x-packages :xpkgs=xpkgs v-if="xpkgs" />
  </div>
</template>

